import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DemoLazyComponent } from './demo-lazy.component';

const routes: Routes = [
  {
    path: '',
    component: DemoLazyComponent,
    children: [
      {
        path: 'child',
        loadChildren: () => import('./demo-lazy-child/demo-lazy-child.module').then(m => m.DemoLazyChildModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DemoLazyRoutingModule { }
