import { Component, OnInit } from '@angular/core';
import { ApiService, ENDPOINT } from '../core/services/api.service';

@Component({
  selector: 'app-demo-lazy',
  templateUrl: './demo-lazy.component.html',
  styleUrls: ['./demo-lazy.component.scss']
})
export class DemoLazyComponent implements OnInit {

  data: any;

  events: any;
  more = 2;

  constructor(
    private apiService: ApiService
  ) { }

  ngOnInit() {
    this.apiService.get(ENDPOINT.users, {page: 2}).subscribe(e => {
      this.data = e.data;
      console.log(e);
    }, err => {
      console.log(err);
    });

    this.apiService.getConfigResponse(ENDPOINT.users).subscribe(e => {
      console.log(e);
    }, err => {
      console.log(err);
    });

    this.apiService.getAssets('assets/events.json').subscribe(e => {
      console.log(e);
      this.events = e;
    });
  }

  load() {
    this.more += 2;
  }

}
